body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.title {
  margin-top: 100px;
  /* margin-bottom: 10px; */
}

.box-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.nav-ul {
  background-color: #13aa55;
  overflow: hidden;
  margin: 0px;
  padding: 0px;
  width: 100%;
  position: fixed;
  float: left;
  top: 0;
  z-index: 1; 
}

.title-nav-li {
  display: inline;
  float: left;
  -webkit-transition: .3s;
  transition: .3s;
}

.title-nav-el {
  /* Use this font (more "programmy") or original (more "friendly")? */
  /* font-family: "Courier New", Courier, monospace;*/
  font-family: "Helvetica Neue", Helvetica, monospace;
  background-color: #13aa55;
  border-bottom: 5px solid #13aa55;
  padding: 1.2rem;
  color: white;
  display: inline-block;
  float: left;
  text-decoration: none;
  font-size: 1.3em;
  padding: none;
  font-weight: bold;
  -webkit-transition: .3s;
  transition: .3s;
}

.title-nav-el:hover {
  color: #11f;
  font-size: 1.4em;
  -webkit-transition: .3s;
  transition: .3s;
}

.logo {
  padding-top: 0.2em;
  cursor: pointer;
  -webkit-transition: .2s;
  transition: .2s;
}

.logo:hover {
  width: 55px;
  height: 55px;
  -webkit-transition: .2s;
  transition: .2s;
}

.nav-li {
  display: inline;
  float: left;
}

.nav-el {
  display: inline-block;
  padding: 1.2em;
  text-decoration: none;
  background-color: #13aa55;
  border-bottom: 2px solid #13aa55;
  color: white;
  -webkit-transition: .3s;
  transition: .3s;
}

.nav-el:hover {
  border-bottom: 2px solid #11f;
  text-decoration: none;
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
}

.nav-el:hover > a {
  color: #11f;
  margin-bottom: 10px ;
  font-size: 18px;
  -webkit-transition: .3s;
  transition: .3s;
}

.nav-el > a {
  text-decoration: none;
  color: white;
  -webkit-transition: .3s;
  transition: .3s;
}


